.hero {
	padding: 87px 0 98px;
	&__inner {}
	&__wrap {
		display: table;
		table-layout: fixed;
		width: 100%; }
	&__left,
	&__right {
		display: table-cell; }
	&__left {
		padding: 0 16px 16px 0;
		width: 53.6%; }
	&__right {
		width: 46.4%;
		vertical-align: bottom;
		@include lg {
			display: none; } }
	&__title {
		max-width: 495px;
		@include lg {
			max-width: 795px;
			br {
				display: none; } } }
	&__btn-shade {
		margin: 6px 0 0 -10px; }
	&__btn {
		margin-top: 4px; }
	&__img {
		margin: 67px 0 -192px -50px;
		max-width: 1310px;
		width: 100vw; } }
.img {
	@include hover {
		.img__inner {
			transform: scale(.95);
			img {
				transform: scale(1.15); } } }
	&__inner {
		overflow: hidden;
		transition: transform .5s ease-in-out; }
	img {
		max-width: 100%;
		display: block;
		transition: transform .5s ease-in-out; } }

.proggres-slider {
	display: inline-block;
	&__slide {
		&:not(:first-child) {
			display: none; } }
	&__bar {
		position: relative;
		margin-top: 24px;
		height: 1px;
		overflow: hidden;
		background-color: rgba($black, 0.12); }
	&__bar-in {
		position: absolute;
		top: 0;
		left: -65%;
		height: 1px;
		width: 100%;
		background-color: $black; } }
.sm-banner {
	position: relative;
	padding: 51px 37px 49px 33px;
	line-height: 1;
	vertical-align: top;
	color: $black;
	z-index: 1;
	display: block;
	@include clr;
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
		transition: transform 0.4s ease-in-out;
		background-color: $yellow; }
	@include hover {
		&:before {
			transform: scale(1.1); } }
	&__number {
		float: left;
		@include f('rt', 60);
		letter-spacing: -0.003em; }
	&__wrap {
		float: left;
		margin-top: 4px;
		padding-left: 5px;
		line-height: 1.1;
		@include f('ob', 24);
		letter-spacing: 0.002em; } }
